import React from 'react'
import s from 'styled-components'
import { Row, Col } from 'react-bootstrap'

import { POPPINS_REGULAR } from '../styles/fonts'

const FooterText = s.p`
  ${POPPINS_REGULAR}
  font-size: 80%;
`

const LeftSections = ['Home', 'Team', 'Products', 'Blog']

const RightSections = [
  {
    title: 'Projects @ The DP'
  },
  {
    title: 'The Daily Pennsylvanian'
  },
  {
    title: '34th Street Magazine'
  },
  {
    title: 'Under the Button'
  }
]

const StyledRow = s(Row)`
  padding: 3rem 3rem;
  background-color: rgba(224, 224, 224, 0.25);
`

const LeftCol = s(Col)`
  border-right: 1px solid rgba(224,224,224,0.5);

  @media screen and (max-width: 768px) {
    border-right: none;
  }
`

const RightCol = s(Col)`
  margin-left: 2rem;

  @media screen and (max-width: 768px) {
    margin-left: 0;
  }
`

const ApplyButton = s.div`
  background-color: #19BAFD;
  ${POPPINS_REGULAR}
  border-radius: 6px;
  width: 5rem;
  text-alignment: center;
  padding: 0.2rem 1rem;
  color: white;
`

const ColWithMargin = s(Col)`
  @media screen and (max-width: 768px) {
    margin-bottom: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid rgba(224,224,224,0.5);
  }
`

export const Footer = () => (
  <StyledRow>
    <ColWithMargin md={6}>
      <FooterText>
        Made with 💖 by the DP Tech Department <br /> &copy; The Daily
        Pennsylvanian 2021
      </FooterText>
      <ApplyButton> APPLY </ApplyButton>
    </ColWithMargin>
    <Col md={6}>
      <Row>
        <LeftCol md={4}>
          {LeftSections.map(section => (
            <FooterText> {section} </FooterText>
          ))}
        </LeftCol>
        <RightCol>
          {RightSections.map(({ title }) => (
            <FooterText> {title} </FooterText>
          ))}
        </RightCol>
      </Row>
    </Col>
  </StyledRow>
)
